<template>
  <div>
    <div class="item" v-for="(item,index) of list" :key="index">
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-if="item.children" class="item-children">
        <detail-list :list="item.children"></detail-list>
        <!-- 组件的递归操作 -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailList",
  props: {
    list: {
      type: Array
    }
  }
};
</script>

<style lang="stylus" scoped>
.item-title {
  line-height: 0.8rem;
  font-size: 0.32rem;
  padding: 0 0.2rem;

  .item-title-icon {
    display: inline-block;
    width: 0.36rem;
    height: 0.36rem;
    background: url('../../../assets/images/333.jpg') center center / cover no-repeat;
    margin-right: 0.1rem;
    vertical-align: middle;
    background-size: 0.4rem 3rem;
    border-radius: 0.6rem;
  }
}

.item-children {
  padding: 0 0.1rem;
}
</style>


